.projects {
  background: white;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.project-card {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  transition: all 0.4s ease;
  border: 1px solid #e2e8f0;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.project-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.project-card:hover .project-image img {
  transform: scale(1.1);
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(102, 126, 234, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: white;
  color: #667eea;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.project-link:hover {
  background: #667eea;
  color: white;
  transform: translateY(-2px);
}

.project-content {
  padding: 2rem;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.project-header h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #2c3e50;
  line-height: 1.3;
}

.project-year {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 600;
}

.project-description {
  color: #666;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.project-features {
  margin-bottom: 1.5rem;
}

.project-features h4 {
  color: #2c3e50;
  margin-bottom: 0.8rem;
  font-size: 1rem;
  font-weight: 600;
}

.project-features ul {
  list-style: none;
  padding: 0;
}

.project-features li {
  color: #555;
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1.5rem;
}

.project-features li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #667eea;
  font-weight: bold;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-badge {
  background: #f1f5f9;
  color: #475569;
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid #e2e8f0;
}

.project-status {
  display: flex;
  justify-content: flex-end;
}

.status-badge {
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.completed {
  background: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.projects-cta {
  text-align: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 3rem;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}

.projects-cta::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%) translateY(-100%) rotate(45deg); }
  100% { transform: translateX(100%) translateY(100%) rotate(45deg); }
}

.projects-cta h3 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  position: relative;
  z-index: 2;
}

.projects-cta p {
  margin-bottom: 1.5rem;
  opacity: 0.9;
  position: relative;
  z-index: 2;
}

.projects-cta .btn {
  background: white;
  color: #667eea;
  position: relative;
  z-index: 2;
}

.projects-cta .btn:hover {
  background: #f8fafc;
  transform: translateY(-2px);
}

.projects-cta .btn i {
  margin-right: 0.5rem;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .project-card {
    margin: 0 auto;
    max-width: 100%;
  }

  .project-content {
    padding: 1.5rem;
  }

  .project-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }

  .project-tech {
    gap: 0.3rem;
  }

  .tech-badge {
    font-size: 0.7rem;
    padding: 0.3rem 0.6rem;
  }

  .projects-cta {
    padding: 2rem;
  }

  .projects-cta h3 {
    font-size: 1.5rem;
  }
}